export default{
  data() {
    return {
      list: ''
    }
  },
  onLoad() {
    this.getDetail()
  },
  methods: {
    getDetail() {
      let that= this
      that.$httpRequest({
        url: that.$api.useCouponList
      }).then(res => {
        that.list= res.data.list
      })
    },
    goStatistics() {
      uni.navigateTo({
        url: './couponStatistics'
      })
    }
  }
}
